}

.table__tr {
  height: 80px;
}
.table__tr:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25);
  transition: all 0.5s ease 0s;
  cursor: pointer;
}

.table__th {
  padding-top: 25px;
  padding-right: 10px;
  padding-bottom: 25px;
